// Primary
body {
  background: #4a4f6f;
}

.container {
  max-width: 800px;
  margin: 0 auto;
}

.wrap {
  //background: url('../img/coffee-bg.jpg') no-repeat center center;
  background-size: cover;
  padding: 30px 0;
  margin-bottom: 7vh;
}

.cw {
  max-width: 554px;
  padding: 0 15px;
  margin: 0 auto;

  &__img {
    position: relative;

    img {
      margin-bottom: -5px;
    }
  }

  &__btn {
    position: absolute;
    background: #fff;
    width: 35px;
    height: 35px;
    right: -30px;
    top: 10px;
    display: flex;
    justify-content: center;
    text-decoration: none;
    @include transition(background, .3s, ease);

    &--like {
      right: 3%;
    }

    &--share {
      right: 15%;
      margin-right: 10px;
    }

    &:hover {
      background: darken(#fff, 10%);
    }

    @include breakpoint(small) {
      width: 50px;
      height: 50px;

      &--share {
        margin-right: 0;
      }
    }

    @media (min-width: 710px) {
        width: 50px;
        height: 50px;
        right: -30px;

        &--like {
          top: 20px;

        }

        &--share {
          top: 80px;
      }
    }
  }

  &__header {
    background: #fff;
    padding: 15px;
    display: flex;
    flex-wrap: nowrap;

    @media (min-width: 560px) {
      padding: 30px 40px;
    }
  }

  &__title {
    position: relative;
    flex-basis: 82%;

    &--dot {
      margin-right: 25px;

      &:after {
        position: absolute;
        width: 13px;
        height: 13px;
        background-color: $color-primary;
        top: 7px;
        overflow: hidden;
        content: "";
        display: block;
      }
    }

    &--heading {
      margin-top: 0;
    }

    p {
      margin: 0;
    }

    @include breakpoint(small) {
      &--dot {
        margin-right: 46px;

        &:after {
          width: 16px;
          height: 16px;
          top: 8px;
        }
      }
    }
  }

  &__price-box {
    width: 60px;
    height: 60px;
    border-radius: 2px;
    background-color: $color-secondary;
    display: flex;
    justify-content: center;

    @include breakpoint(small) {
      width: 85px;
      height: 85px;
    }
  }

  &__price-number {
    align-self: center;
    color: #fff;
    font-size: 20px;

    @include breakpoint(small) {
      font-size: 26px;
    }
  }
}

