// Coffee Widget Messsge
.cw__message {
  background: #eff2f2;
  padding: 15px;
  display: flex;
  flex-wrap: nowrap;
  position: relative;

  &:after {
    position: absolute;
    background: #ced0d5;
    content: "";
    display: block;
    width: 2px;
    height: 40px;
    top: 15px;
    right: 8px;
  }

  @include breakpoint(small) {
    padding: 22px 40px;

    &:after {
      height: 55px;
    }
  }

}

.cw__portrait {
  margin-right: 15px;
}

.cw__message-text {
  flex-basis: 82%;

  p {
    margin-bottom: 5px;
  }
}

@include breakpoint(small) {
  .cw__portrait {
    margin-right: 35px;
  }
}

// Coffee Widget Comment

.cw__comment {
  background: #fff;
  padding: 15px 20px;

  form {
    height: 40px;
  }

  textarea {
    width: 85%;
    height: 40px;
    font-size: 14px;
    border: none;
    border-bottom: 1px solid transparent;
    color: #76898d;
    @include transition(all, .2s, ease);

    &:focus {
      border: none;
      border-bottom: 1px solid #ced0d5;
      outline: none;
    }

  }

  .cw__input {
    /* reset input */
    margin: 0;
    padding: 0;
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    background: none;
    border: none;
    border-radius: none;
    box-shadow: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    outline: none;
    cursor: pointer;

    float: right;
  }

  @include breakpoint(small) {
    padding: 15px 40px;

    form {
      height: 25px;
    }
    textarea {
      height: 25px;
      font-size: 16px;
    }
  }
}


/* non-project related stuff */
footer {
  max-width: 555px;
  margin: 0 auto;
  padding: 0 13px;

  p {
    color: #fff;
    font-size: 15px;
  }

  a {
    color: #2ecca1;
    @include transition(color, .3s, ease);

    &:hover {
      color: darken(#2ecca1, 10%);
    }
  }
}